@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@700&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 14px;
  margin: 0;
  margin-top: 25vh;
  padding: 0 2em;
  font-family: "Red Hat Text", sans-serif;
  background-color: #191a24;
  color: white;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  background-image: url("../../../images/bg-stars.svg");
  background-repeat: no-repeat;
}

h1 {
  margin-top: 0;
  margin-bottom: 2.5em;
  letter-spacing: 0.4em;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.8em;
}
@media only screen and (min-width: 800px) {
  h1 {
    font-size: 2.2em;
    margin-bottom: 4em;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

.footer {
  background-image: url("../../../images/pattern-hills.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right;
  padding: 12vh 0;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.footer__socials {
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateY(2em);
}
.footer__socials-item {
  padding: 0 2em;
}
@media only screen and (min-width: 800px) {
  .footer__socials-item:hover {
    filter: invert(48%) sepia(27%) saturate(1104%) hue-rotate(295deg) brightness(97%) contrast(89%);
  }
}

.container-timer__numbers {
  display: flex;
  justify-content: center;
  text-align: center;
}
.container-timer__card {
  padding: 0 1em;
}
.container-timer__card-number {
  color: #fb6087;
  padding: 0.3em 0.5em;
  font-size: 2.5em;
  z-index: 5;
  border-radius: 10px;
  position: relative;
}
@media only screen and (min-width: 800px) {
  .container-timer__card-number {
    font-size: 5em;
  }
}
.container-timer__card-number::before {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 50%;
  background-color: #2D2C44;
  border-radius: 3px 3px 6px 6px;
}
.container-timer__card-number::after {
  border-radius: 6px 6px 2px 2px;
  z-index: -1;
  position: absolute;
  bottom: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 50%;
  background-color: #343650;
}
.container-timer__card-suffix {
  margin-top: 2em;
  text-transform: uppercase;
  font-size: 0.6em;
  letter-spacing: 0.3rem;
  color: #8486a9;
}
@media only screen and (min-width: 800px) {
  .container-timer__card-suffix {
    font-size: 1em;
  }
}